import { formatStringToTime, FormatStringToShortDate, getPointDuration, getCheckedOffers } from '../utils/utils.js';


function createEventOffersTemplate(pointOffers) {
  let eventOffersTemplate = '';

  if (pointOffers.length === 0) {
    return '';
  }

  pointOffers.forEach((offer) => {
    if (offer) {
      const { title, price } = offer;

      eventOffersTemplate += `
        <li class="event__offer">
          <span class="event__offer-title">${title}</span>
          &plus;&euro;&nbsp;
          <span class="event__offer-price">${price}</span>
        </li>`;
    }
  });

  return eventOffersTemplate;
}


function createWayPointTemplite({ point, pointDestination, pointOffers }) {
  const { basePrice, dateFrom, dateTo, isFavorite, type } = point;
  const offf = pointOffers.find((e) => e.type === type).offers;
  const checkedOffers = getCheckedOffers(point.offers, offf);

  const dest = pointDestination.find((e) => e.id === point.destination);
  const favoriteClassName = isFavorite
    ? 'event__favorite-btn event__favorite-btn--active'
    : 'event__favorite-btn';

  return `<li class="trip-events__item">
  <div class="event">
    <time class="event__date" datetime=${FormatStringToShortDate(dateFrom)}>${FormatStringToShortDate(dateFrom)}</time>
    <div class="event__type">
      <img class="event__type-icon" width="42" height="42" src="img/icons/${type}.png" alt="Event type icon">
    </div>
    <h3 class="event__title">${type} ${dest.name}</h3>

    <div class="event__schedule">
      <p class="event__time">
        <time class="event__start-time" datetime=${formatStringToTime(dateFrom)}>${formatStringToTime(dateFrom)}</time>
        &mdash;
        <time class="event__end-time" datetime=${formatStringToTime(dateTo)}>${formatStringToTime(dateTo)}</time>
      </p>
      <p class="event__duration">${getPointDuration(dateFrom, dateTo)}</p>
    </div>
    <p class="event__price">
      &euro;&nbsp;<span class="event__price-value">${(basePrice)}</span>
    </p>
   <h4 class="visually-hidden">Offers:</h4>
        <ul class="event__selected-offers">
          ${createEventOffersTemplate(checkedOffers)}
        </ul>
    <button class="${favoriteClassName}" type="button">
          <span class="visually-hidden">Add to favorite</span>
      <svg class="event__favorite-icon" width="28" height="28" viewBox="0 0 28 28">
        <path d="M14 21l-8.22899 4.3262 1.57159-9.1631L.685209 9.67376 9.8855 8.33688 14 0l4.1145 8.33688 9.2003 1.33688-6.6574 6.48934 1.5716 9.1631L14 21z"/>
      </svg>
    </button>
    <button class="event__rollup-btn" type="button">
      <span class="visually-hidden">Open event</span>
    </button>
  </div>
</li>`;
}
export { createWayPointTemplite };
